function createTextElement(text) {
    return {
        type: "TEXT_ELEMENT",
        props: {
            nodeValue: text,
            children: [],
        },
    };
}

function createElement(type, props, ...children) {
    return {
        type,
        props: {
            ...props,
            children: children.map(child =>
                typeof child === "object"
                    ? child
                    : {
                        type: "TEXT_ELEMENT",
                        props: {
                            nodeValue: child,
                            children: [],
                        },
                    }
            ),
        },
    };
}


// إنشاء عناصر DOM حقيقية من العناصر الافتراضية
function createDom(fiber) {
    const dom =
        fiber.type === "TEXT_ELEMENT"
            ? document.createTextNode("")
            : document.createElement(fiber.type);

    updateDom(dom, {}, fiber.props);

    return dom;
}

// تحديث خصائص DOM
function updateDom(dom, prevProps, nextProps) {
    // مثال بسيط: تحديث النصوص فقط
    if (dom.nodeType === Node.TEXT_NODE) {
        if (prevProps.nodeValue !== nextProps.nodeValue) {
            dom.nodeValue = nextProps.nodeValue;
        }
    }

    // إزالة الخصائص القديمة
    Object.keys(prevProps)
        .filter(key => key !== "children" && !(key in nextProps))
        .forEach(name => {
            if (name.startsWith("on")) {
                dom.removeEventListener(
                    name.toLowerCase().substring(2),
                    prevProps[name]
                );
            } else {
                dom[name] = "";
            }
        });

    // إضافة أو تحديث الخصائص الجديدة
    Object.keys(nextProps)
        .filter(key => key !== "children" && key !== "nodeValue")
        .forEach(name => {
            // معالجة أحداث DOM
            if (name.startsWith("on")) {
                const eventType = name.toLowerCase().substring(2);
                if (prevProps[name]) {
                    dom.removeEventListener(eventType, prevProps[name]);
                }
                dom.addEventListener(eventType, nextProps[name]);
            } else {
                dom[name] = nextProps[name];
            }
        });
}

// تنفيذ المكون في DOM
function render(element, container) {
    // نبدأ بإعداد الفايبر الرئيسي
    wipRoot = {
        dom: container,
        props: {
            children: [element],
        },
        alternate: currentRoot,
    };
    deletions = [];
    nextUnitOfWork = wipRoot;
    // سنحتاج إلى جدولة عمل التحديث
    requestIdleCallback(workLoop);
}

// المتغيرات العامة لنظام العمل
let nextUnitOfWork = null;
let currentRoot = null;
let wipRoot = null;
let deletions = [];

// حلقة العمل الرئيسية - تعمل خلال وقت خمول المتصفح
const workLoop = deadline => {
    let shouldYield = false;
    while (nextUnitOfWork && !shouldYield) {
        nextUnitOfWork = performUnitOfWork(nextUnitOfWork);
        shouldYield = deadline.timeRemaining() < 1;
    }

    if (!nextUnitOfWork && wipRoot) {
        commitRoot(); // هنا لازم تتأكد إن دي بتحدث الـ DOM
    }

    requestIdleCallback(workLoop);
};

// تنفيذ وحدة عمل واحدة وإرجاع الوحدة التالية
function performUnitOfWork(fiber) {
    // التحقق مما إذا كان المكون وظيفيًا ومعالجته بشكل مختلف
    const isFunctionComponent = typeof fiber.type === "function";

    if (isFunctionComponent) {
        updateFunctionComponent(fiber);
    } else {
        updateHostComponent(fiber);
    }

    // إرجاع وحدة العمل التالية - ابن، أخ، ثم عم
    if (fiber.child) {
        return fiber.child;
    }
    let nextFiber = fiber;
    while (nextFiber) {
        if (nextFiber.sibling) {
            return nextFiber.sibling;
        }
        nextFiber = nextFiber.parent;
    }
}

// معالجة المكونات الوظيفية
function updateFunctionComponent(fiber) {
    wipFiber = fiber;
    hookIndex = 0;
    wipFiber.hooks = [];

    // استدعاء المكون الوظيفي للحصول على العناصر
    const children = [fiber.type(fiber.props)];
    reconcileChildren(fiber, children);
}

// معالجة عناصر DOM العادية
function updateHostComponent(fiber) {
    if (!fiber.dom) {
        fiber.dom =
            fiber.type === "TEXT_ELEMENT"
                ? document.createTextNode(fiber.props.nodeValue)
                : document.createElement(fiber.type);
    }

    // نربط الأحداث والخصائص (لو عندك دالة updateDom استعملها)
    if (fiber.props) {
        updateDom(fiber.dom, {}, fiber.props);
    }

    // نبني شجرة الفايبر للأطفال
    const elements = fiber.props.children || [];
    reconcileChildren(fiber, elements);
}

// مطابقة العناصر الجديدة مع الفايبرز الحالية
function reconcileChildren(wipFiber, elements) {
    // تأكد من أن elements ليس undefined
    elements = elements || [];

    let index = 0;
    let oldFiber = wipFiber.alternate && wipFiber.alternate.child;
    let prevSibling = null;

    while (index < elements.length || oldFiber != null) {
        const element = elements[index];
        let newFiber = null;

        // تنفيذ منطق المقارنة والتوفيق
        const sameType = oldFiber && element && element.type === oldFiber.type;

        if (sameType) {
            // تحديث
            newFiber = {
                type: oldFiber.type,
                props: element.props,
                dom: oldFiber.dom,
                parent: wipFiber,
                alternate: oldFiber,
                effectTag: "UPDATE",
            };
        }
        if (element && !sameType) {
            // إنشاء
            newFiber = {
                type: element.type,
                props: element.props,
                dom: null,
                parent: wipFiber,
                alternate: null,
                effectTag: "PLACEMENT",
            };
        }
        if (oldFiber && !sameType) {
            // حذف
            oldFiber.effectTag = "DELETION";
            deletions.push(oldFiber);
        }

        if (oldFiber) {
            oldFiber = oldFiber.sibling;
        }

        if (index === 0) {
            wipFiber.child = newFiber;
        } else if (element) {
            prevSibling.sibling = newFiber;
        }

        prevSibling = newFiber;
        index++;
    }
}

// تطبيق التغييرات على DOM الحقيقي
function commitRoot() {
    deletions.forEach(commitWork);
    commitWork(wipRoot.child);
    currentRoot = wipRoot;
    wipRoot = null;
}

// تنفيذ العمل على عنصر واحد
function commitWork(fiber) {
    if (!fiber) return;

    // للمكونات الوظيفية، نحتاج إلى العثور على أقرب أب له DOM
    let domParentFiber = fiber.parent;
    while (domParentFiber && !domParentFiber.dom) {
        domParentFiber = domParentFiber.parent;
    }

    // تأكد من وجود domParentFiber
    if (domParentFiber) {
        const domParent = domParentFiber.dom;

        if (fiber.effectTag === "PLACEMENT" && fiber.dom != null) {
            domParent.appendChild(fiber.dom);
        } else if (fiber.effectTag === "UPDATE" && fiber.dom != null) {
            updateDom(fiber.dom, fiber.alternate.props, fiber.props);
        } else if (fiber.effectTag === "DELETION") {
            commitDeletion(fiber, domParent);
        }
    }

    commitWork(fiber.child);
    commitWork(fiber.sibling);
}

function commitDeletion(fiber, domParent) {
    if (fiber.dom) {
        domParent.removeChild(fiber.dom);
    } else {
        // إذا كان مكونًا وظيفيًا، علينا العثور على DOM فعلي
        commitDeletion(fiber.child, domParent);
    }
}

const Fragment = "RAVU_FRAGMENT";

function withErrorFallback(Component, Fallback) {
    return function WrappedComponent(props) {
        try {
            return Component(props);
        } catch (error) {
            console.error("Component Error:", error);
            return typeof Fallback === 'function' ? Fallback({ error }) : Fallback;
        }
    };
}

// إضافة دعم للحالة والـ hooks
let wipFiber = null;
let hookIndex = null;

function $State(initial) {
    // استخدام Ravu مباشرة لضمان إن القيم الأخيرة متاحة
    const oldHook =
        wipFiber.alternate &&
        wipFiber.alternate.hooks &&
        wipFiber.alternate.hooks[hookIndex];

    const hook = {
        state: oldHook ? oldHook.state : initial,
        queue: [],
    };

    if (oldHook) {
        oldHook.queue.forEach(action => {
            hook.state = typeof action === 'function' ? action(hook.state) : action;
        });
        // فَرّغ الكيو عشان ما تتطبقش التحديثات مرة تانية
        oldHook.queue = [];
    }

    const setState = action => {
        // ادفع الـ action للـ queue الخاص بالـ hook
        hook.queue.push(action);

        if (!currentRoot || !currentRoot.dom) {
            console.warn("Current root DOM is not available. Make sure you've rendered the app correctly.");
            return;
        }

        // حضر شجرة Fiber جديدة علشان إعادة الريندر
        wipRoot = {
            dom: currentRoot.dom,
            props: currentRoot.props,
            alternate: currentRoot,
        };
        nextUnitOfWork = wipRoot;
        deletions = [];

        requestIdleCallback(workLoop);
    };

    wipFiber.hooks.push(hook);
    hookIndex++;
    return [hook.state, setState];
}

const Ravu = {
    createElement,
    render,
    $State,
    wipFiber,
    withErrorFallback,
    hookIndex,
    Fragment
};

export default Ravu;